    changeDetection : ChangeDetectionStrategy.OnPush  // ÑÐ²Ð½Ð°Ñ ÑÑÑÐ°ÑÐµÐ³Ð¸Ñ ÑÐ¸ÐºÑÐ°ÑÐ¸Ð¸ Ð¸Ð·Ð¼ÐµÐ½ÐµÐ½Ð¸Ð¹
